import { useState } from 'react';
import { Button, Form, Input, message} from 'antd';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import NavBar from '../Components/NavBar'

const Post = () => {
    const navigate = useNavigate()

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [name, setName] = useState('')
    const [category, setCategory] = useState('')
    const [image, setImage] = useState('')
    const [price, setPrice] = useState('')
    const [email, setEmail] = useState('')
    const [APIkey, setAPIkey] = useState('')
    const [description, setDescription] = useState('')
    const [html, setHtml] = useState('')
    const [css, setCss] = useState('')
    const [js, setJs] = useState('')

    const finishButton = async() => {
        axios.get(`/acc/find/${username}/${password}`).then((res) => {
          if(res.data.length === 0){
            message.error("Error account not found")
          }
          else{
            message.success("Account found, posted")
            navigate("/")
            axios.post("/code/post", {username, name, email, category, image, html, css, js, description, APIkey, price}).then((res2) => {
              message.success("Posted")
            }).catch(error => message.error("Could not be posted "))
          }
          })
      }

    const submitCode = () => {
        const htmlCode = document.querySelector(".editor #html-code").value;
        const cssCode = "<style>" + document.querySelector(".editor #css-code").value + "</style>";
        const jsCode = document.querySelector(".editor #js-code").value;
        const output = document.querySelector(".editor #output");

        output.contentDocument.body.innerHTML = htmlCode+cssCode;
        output.contentWindow.eval(jsCode)
    }
    return (
    <NavBar>
    <Form className='posting-form'>
        <Row>
            <Form.Item label="Project name" >
            <Input onChange={(e) => setName(e.target.value)} />
            </Form.Item>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Username">
                    <Input onChange={(e) => setUsername(e.target.value)} placeholder="Your username..." />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Password" >
                    <Input.Password onChange={(e) => setPassword(e.target.value)} placeholder='your password...' />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Category" >
                    <Input onChange={(e) => setCategory(e.target.value)} placeholder='category' />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="Image" >
                    <Input onChange={(e) => setImage(e.target.value)} placeholder="Image URL" />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="Price" >
                    <Input onChange={(e) => setPrice(e.target.value)} placeholder='price in usd' />
                </Form.Item>
            </Col>
            <Col>
                <Form.Item label="email" >
                    <Input onChange={(e) => setEmail(e.target.value)} placeholder='email' />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <Form.Item label="APIket" >
                    <Input onChange={(e) => setAPIkey(e.target.value)} placeholder="Your stripe secret key" />
                </Form.Item>
            </Col>
        </Row>
        <Row>
            <Col>
                <textarea label="description" onChange={(e) => setDescription(e.target.value)} placeholder='enter your description here' rows={5} cols={260} ></textarea>
            </Col>
        </Row>
        <div class="editor">
                <Row>
              <Col>
                <h2>HTML code</h2>
                <hr />
                <br />
                <textarea placeholder='enter your html code here' onChange={(e) => setHtml(e.target.value)} id="html-code" rows={20} cols={70}></textarea>
              </Col>
              <Col>
                <h2>CSS code</h2>
                <hr />
                <br />
                <textarea placeholder='enter your css code here' onChange={(e) => setCss(e.target.value)} id="css-code" rows={20} cols={70}></textarea>
              </Col>
              <Col>
                <h2>JS code</h2>
                <hr />
                <br />
                <textarea placeholder='enter your js code here' onChange={(e) => setJs(e.target.value)} id="js-code" rows={20} cols={70}></textarea>
              </Col>
            </Row>
            <Button onClick={submitCode}>Run</Button>
            <Row>
              <Col>
                <iframe title={name} style={{width: 1000, height: 300}} id="output"></iframe>
              </Col>
            </Row>
            </div>
            <Button onClick={finishButton} >Submit</Button>
    </Form>
    </NavBar>
  );
};
export default Post;